<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>密码设置</h1>
            </v-header>
        </div>
        <div class="setPwd-container">
            <div class="cell-wrap">
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">新密码</span>
                    <input type="password"
                           placeholder="请输入新密码"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.newPwd"
                           maxlength="20"
                    >
                </v-cell-box>
                <v-cell-box :hasBorder="!forgetInfo.hasBindCard">
                    <span class="title" slot="left">确认新密码</span>
                    <input type="password"
                           placeholder="请再次输入密码"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.reNewPwd"
                           maxlength="20"
                    >
                </v-cell-box>
                <v-cell-box v-show="!forgetInfo.hasBindCard">
                    <span class="title" slot="left">验证码</span>
                    <input type="tel"
                           placeholder="请输入验证码"
                           class="input-box"
                           slot="middle"
                           v-model="forgetInfo.checkNum"
                           maxlength="6"
                    >
                    <div class="check-num-btn" slot="right">
                        <button class="btn" :disabled="checkNumCutTime !== ''" @click="sendCheckNum">
                            <span>{{checkNumCutTime}}</span><span v-show="checkNumCutTime">s</span><span>{{checkNumTxt}}</span>
                        </button>
                    </div>
                </v-cell-box>
            </div>
            <div class="tips" v-show="showTips">
                <div class="symbol-wrap">
                    <v-symbol :name="'warn'"></v-symbol>
                </div>
                <p>两次密码不一致!</p>
            </div>
            <div class="tips-txt">
                <p class="title">温馨提示</p>
                <p class="txt">密码设置长度为8-12位，必须包含字母，且同时包含数字、字母、符号中任意两种以上字符。
</p>
            </div>
            <div class="btn-wrap">
                <v-linear-button btnTxt="下一步" :disabled="!canNext" @click="setPwd"></v-linear-button>
            </div>
        </div>
        <v-popup-alert v-model="showAlert" @click="jumpBackLogin">
            <p class="alert-txt">您的登录密码已设置成功 请用新密码登录</p>
        </v-popup-alert>
        <v-toast v-model="showToast"
                 :toastTxt="toastTxt"
        ></v-toast>
    </v-page>
</template>

<script>
    import {vCellBox} from '../../component/cell'
    import {vLinearButton} from '../../component/linearButton'
    import {vPopupAlert} from '../../component/popupAlert'
    export default {
        name: 'setPwd',
        components: {
            vCellBox,
            vLinearButton,
            vPopupAlert
        },
        computed: {
            showTips () {
                return this.forgetInfo.newPwd !== '' &&
                    this.forgetInfo.reNewPwd !== '' &&
                    this.forgetInfo.newPwd !== this.forgetInfo.reNewPwd
            },
            canNext () {
                return this.forgetInfo.newPwd !== '' &&
                    this.forgetInfo.reNewPwd !== '' &&
                    this.forgetInfo.newPwd === this.forgetInfo.reNewPwd &&
                    ((!this.forgetInfo.hasBindCard &&
                    this.forgetInfo.checkNum !== '') ||
                    this.forgetInfo.hasBindCard)
            }
        },
        data () {
            return {
                forgetInfo: {
                    phone: '',
                    // 新密码
                    newPwd: '',
                    // 再次输入的新密码
                    reNewPwd: '',
                    // 短信验证码
                    checkNum: '',
                    // 是否有绑卡
                    hasBindCard: false
                },
                // 弹出提示框
                showAlert: false,
                // toast提示框信息
                toastTxt: '',
                // 是否显示toast提示框
                showToast: false,
                // 发送验证码按钮文本
                checkNumTxt: '获取验证码',
                // 发送倒计时
                checkNumCutTime: '',
                // 验证码定时器对象
                checkNumTimer: null,
            }
        },
        methods: {
            // 获取上个页面传递的参数
            getParamsFromPre () {
                window.T.Native.getPageParams().then((ret) => {
                    this.forgetInfo = Object.assign(this.forgetInfo, ret.params)
                }).catch((ret) => {
                    console.warn(JSON.stringify(ret))
                })
            },
            // 跳转到登陆页
            jumpBackLogin () {
                let obj = {
                    back: 'login/loginPwd/index'
                }
                window.T.back(obj)
            },
            // 设置登陆密码
            setPwd () {
                this.showPopupAlert()
            },
            // 显示alert提示框
            showPopupAlert () {
                this.showAlert = true
            },
            // 发送短信验证码
            sendCheckNum () {
                // 校验手机号
                let isPhoneNum = window.T.Validator.phone(this.forgetInfo.phone, '手机号')
                if (isPhoneNum !== true) {
                    this.toastTxt = isPhoneNum
                    this.showToast = true
                    return
                }
                this._checkNumCutTime('checkNumTxt','checkNumCutTime', 'checkNumTimer')
            },
            // 短信验证码倒计时
            _checkNumCutTime (btnTxtKey, timeKey, timerKey) {
                this[timeKey] = '60'
                let oldBtnTxt = this[btnTxtKey]
                this[btnTxtKey] = '重新发送'
                this[timerKey] = setInterval(() => {
                    this[timeKey] --
                    if (this[timeKey] < 1) {
                        this[timeKey] = ''
                        this[btnTxtKey] = oldBtnTxt
                        clearInterval(timer)
                    }
                }, 1000)
            },
        },
        created () {
            this.getParamsFromPre()
        }
    }
</script>

<style lang="scss" scoped>
    .setPwd-container {
        padding-top: px2rem(20);
        padding-bottom: px2rem(40);
        .cell-wrap {
            font-size: px2rem(32);
            flex: none;
            span {
                display: block;
            }
            .title {
                width: px2rem(230);
                line-height: px2rem(44);
            }
            .info {
                line-height: px2rem(36);
            }
            input {
                width: 100%;
                font-size: px2rem(32);
            }
            .check-num-btn {
                color: #F28300;
                font-family: PingFangSC-Medium;
                margin-left: px2rem(10);
                font-size: px2rem(28);
                line-height: px2rem(40);
                span{
                    display: inline;
                }
                .btn {
                    background-color: #ffffff;
                    border: none;
                    height: 1rem;
                    padding: 0 .2rem;
                    color: #F28300;
                    @include font-color(#F28300);
                    &[disabled]{
                        color: #dcbf9d;
                    }
                }
            }
        }
        .tips {
            display: flex;
            flex: none;
            align-items: center;
            height: px2rem(60);
            padding: 0 px2rem(20);
            color: #FF3F3F;
            font-size: px2rem(24);
            line-height: px2rem(40);
            width: 100%;
            box-sizing: border-box;
            .symbol-wrap {
                margin-right: px2rem(12);
                display: flex;
                justify-content: center;
                align-items: center;
                .ui-symbol {
                    height: px2rem(24);
                    width: px2rem(24);
                }
            }
            p {
                height: .4rem;
                line-height: .4rem;
            }
        }
        .tips-txt {
            margin-top: px2rem(22);
            margin-bottom: px2rem(80);
            padding: 0 px2rem(28);
            font-size: px2rem(24);
            line-height: px2rem(34);
            color: #9B9B9B;
            .title {
                margin-bottom: px2rem(16);
            }
        }
        .btn-wrap {
            padding: 0 px2rem(40);
        }
    }

    .alert-txt {
        font-size: px2rem(32);
        line-height: px2rem(52);
        color: #333333;
        font-family: PingFangSC-Regular;
        text-align: center;
    }
</style>



