<template>
    <transition name="fade">
        <div class="ui-popup-alert" v-show="value">
            <transition name="scale">
                <div class="popup-wrap" v-show="value">
                    <h1 class="title" v-if="title" :style="{  color: titleColor}">{{title}}</h1>
                    <div class="content">
                        <slot></slot>
                    </div>
                    <div class="btn-wrap">
                        <button class="btn-cancel" v-if="hasCancel" @click="handleCancel" :style="{ color: btnCancelColor }">{{btnCancelTxt}}</button>
                        <button class="btn-agree" @click="handleAgree" :style="{ color: btnAgreeColor }">{{btnAgreeTxt}}</button>
                    </div>
                </div>
            </transition>
        </div>
    </transition>
</template>

<script>
    export default {
        name: 'vPopupAlert',
        props: {
            // 是否显示alert框
            value: {
                type: Boolean,
                default: false
            },
            // 标题
            title: {
                type: String,
                default: ''
            },
            // 是否有取消按钮
            hasCancel: {
                type: Boolean,
                default: false
            },
            // 取消按钮文本
            btnCancelTxt: {
                type: String,
                default: '取消'
            },
            // 确定按钮文本
            btnAgreeTxt: {
                type: String,
                default: '确定'
            },
            // 标题颜色
            titleColor: {
                type: String,
                default: '#272727'
            },
            // 确认按钮字体颜色
            btnAgreeColor: {
                type: String,
                default: '#F28300'
            },
            // 取消按钮字体颜色
            btnCancelColor: {
                type: String,
                default: '#9B9B9B'
            }
        },
        methods: {
            // 点击确定按钮
            handleAgree (e) {
                this.closeAlert()
                this.$emit('click', e)
            },
            // 点击取消按钮
            handleCancel (e) {
                this.closeAlert()
                this.$emit('cancel', e)
            },
            // 关闭弹出框
            closeAlert () {
                this.$emit('input', false)
            }
        },
        mounted () {
            document.body.appendChild(this.$el)
        }
    }
</script>

<style lang="scss" scoped>
    .ui-popup-alert{
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, .7);
        z-index: 999999;
        .popup-wrap{
            display: flex;
            flex-direction: column;
            width: px2rem(580);
            border-radius: px2rem(10);
            background-color: #ffffff;
            overflow: hidden;
            .title{
                height: px2rem(120);
                font-size: px2rem(36);
                line-height: px2rem(120);
                text-align: center;
                border-bottom: 1px solid #DBDBDB;
                font-family:PingFangSC-Medium;
            }
            .content{
                padding: px2rem(50);
                max-height: px2rem(500);
                overflow-y: scroll;
                overflow-x: hidden;
                &::-webkit-scrollbar{
                    display: none;
                }
            }
            .btn-wrap{
                height: px2rem(89);
                font-size: px2rem(36);
                line-height: px2rem(89);
                border-top: 1px solid #DBDBDB;
                display: flex;
                justify-content: space-between;
                button{
                    outline: none;
                    height: 100%;
                    flex: 1;
                    background-color: #ffffff;
                    border: none;
                    @include button-color(#ffffff);
                }
                .btn-cancel{
                    border-right: 1px solid #9B9B9B;
                }
            }
        }
    }
    .fade-enter-active, .fade-leave-active{
        transition: opacity .3s;
    }
    .fade-enter, .fade-leave-to{
        opacity: 0;
    }
    .scale-enter-active, .scale-leave-active{
        transition: all .3s;
    }
    .scale-enter{
        transform: scale(1.1);
    }
    .scale-enter-to, .scale-leave{
        transform: scale(1);
    }
</style>



